html {
    .loader {
        background: none repeat scroll 0 0 #ffffff;
        bottom: 0;
        height: 100%;
        left: 0;
        position: fixed;
        right: 0;
        top: 0;
        width: 100%;
        z-index: 9999;

        .loader_inner {
            background-image: url("../images/preloader.gif");
            background-size: cover;
            background-repeat: no-repeat;
            background-position: center center;
            background-color: #fff;
            height: 60px;
            width: 60px;
            margin-top: -30px;
            margin-left: -30px;
            left: 50%;
            top: 50%;
            position: absolute;
        }
    }

    height: 100%;
}

body {
    min-width: 320px !important;
    position: relative;
    line-height: 1.6;
    -webkit-font-smoothing: antialiased;
    overflow-x: hidden;
    height: 100%;
    &:after {
        position: fixed;
        content: '';
        top: 0;
        right: 0;
        width: 184px;
        height: 1152px;
        background-image: url(/images/bg-oil.png);
        background-repeat: repeat-y;
        background-position: right;
        background-size: contain;
        z-index: 2;
        transform: scale(-1, 1);
    }
    &:before {
        position: fixed;
        content: '';
        top: 0;
        left: 0;
        width: 184px;
        height: 1152px;
        background-image: url(/images/bg-oil.png);
        background-repeat: repeat-y;
        background-position: left;
        background-size: contain;
        z-index: 2;
    }
    input {
        &:focus {
            &:required {
                &:invalid {
                    color: red;
                }
            }
        }

        &:required {
            &:valid {
                color: green;
            }
        }
    }

    textarea {
        &:focus {
            &:required {
                &:invalid {
                    color: red;
                }
            }
        }

        &:required {
            &:valid {
                color: green;
            }
        }
    }
}

