    display: -o-flex;
    display: flex;
    flex-wrap: wrap;

    > div {
        box-sizing: border-box;
        margin: -0.3px;
    }
}

/* no flex */
.no-flexbox {
    .row-flex {
        display: block;

        > div {
            display: inline-block;
            float: none;
            box-sizing: border-box;
            vertical-align: top;
            margin: 0 -2.5px auto;
        }
    }
}

