                transition: background-color ease 0.4s;
                color: $b;
                box-sizing: border-box;
                border-radius: 6px;
                display: block;
            }

            &:hover,
            &:focus {
                background-color: transparent;
                border: 2px solid $or;
                a {
                    text-decoration: none;
                    color: $or;
                }
            }
        }

        li.active {
            border: 2px solid $or;
            background-color: $or;
            a {
                background-color: $or;
                color: $w;
            }
            &:hover,
            &:focus {
                background-color: $or;
                border: 2px solid $or;
                a {
                    text-decoration: none;
                }
            }
        }

        li.prev,
        li.next {
            width: 48px;
            height: 40px;
            font-size: 24px;
            line-height: 46px;
            background-color: transparent;
            transition: color ease 0.4s;
            border: 2px solid transparent;
            box-sizing: border-box;
            vertical-align: middle;
            i {
                line-height: 39px;
                color: #BDBDBF;
            }
            &:hover,
            &:focus {
                background-color: transparent;
                border: 2px solid transparent;
                a {
                    text-decoration: none;
                    i {
                        color: $or;
                    }
                }
            }
        }
    }
}
/*forms*/
.base-form {
    .inp-wp, .wrap-textarea {
        position: relative;
    }
    .confid {
        display: table;
        margin-bottom: 15px;
        text-align: left;
        padding-top: 9px;
        p {
            font-size: 11px;
            line-height: 16px;
            color: #2D3140;
            font-family: $os;
            display: inline-block;
            vertical-align: top;
            margin: 0;
            a {
                color: $or;
                &:hover, &:focus {
                    color: $gr;
                    text-decoration: none;
                }
            }
        }
        .left-pos {
            display: table-cell;
            padding-right: 11px;
            vertical-align: middle;
        }
        .right-pos {
            display: table-cell;
            vertical-align: middle;
        }
    }
    .form-body {
        font-size: 0;
        .inp-wp {
            margin-bottom: 32px;
            display: inline-block;
        }
        input {
            width: 100%;
            padding: 0 32px 0 16px;
            background: $w;
            font-size: 15px;
            color: #04040D;
            border: 1px solid #D0D2DB;
            box-sizing: border-box;
            border-radius: 2px;
            height: 40px;
            font-family: $os;
            transition: border ease 0.4s;
            &:hover, &:focus {
                border: 1px solid #4981F2;
                &::-webkit-input-placeholder {color:#04040D;font-size: 15px;font-family: $os;}
                &::-moz-placeholder          {color:#04040D;font-size: 15px;font-family: $os;}/* Firefox 19+ */
                &:-moz-placeholder           {color:#04040D;font-size: 15px;font-family: $os;}/* Firefox 18- */
                &:-ms-input-placeholder      {color:#04040D;font-size: 15px;font-family: $os;}
            }
            &::-webkit-input-placeholder {color:#04040D;font-size: 15px;font-family: $os;}
            &::-moz-placeholder          {color:#04040D;font-size: 15px;font-family: $os;}/* Firefox 19+ */
            &:-moz-placeholder           {color:#04040D;font-size: 15px;font-family: $os;}/* Firefox 18- */
            &:-ms-input-placeholder      {color:#04040D;font-size: 15px;font-family: $os;}
        }
        input.error {
            border: 1px solid #E65B4E;
        }
    }
    .textarea-default {
        width: 100%;
        padding: 9px 16px;
        background: $w;
        font-size: 15px;
        color: #04040D;
        border: 1px solid #D0D2DB;
        box-sizing: border-box;
        border-radius: 2px;
        height: 72px;
        font-family: $os;
        transition: border ease 0.4s;
        resize: none;
        margin-bottom: 24px;
        &:hover, &:focus {
            border: 1px solid #4981F2;
            &::-webkit-input-placeholder {color:#04040D;font-size: 15px;font-family: $os;}
            &::-moz-placeholder          {color:#04040D;font-size: 15px;font-family: $os;}/* Firefox 19+ */
            &:-moz-placeholder           {color:#04040D;font-size: 15px;font-family: $os;}/* Firefox 18- */
            &:-ms-input-placeholder      {color:#04040D;font-size: 15px;font-family: $os;}
        }
        &::-webkit-input-placeholder {color:#04040D;font-size: 15px;font-family: $os;}
        &::-moz-placeholder          {color:#04040D;font-size: 15px;font-family: $os;}/* Firefox 19+ */
        &:-moz-placeholder           {color:#04040D;font-size: 15px;font-family: $os;}/* Firefox 18- */
        &:-ms-input-placeholder      {color:#04040D;font-size: 15px;font-family: $os;}
    }
    label {
        color: #737480;
        font-size: 15px;
        font-family: $os;
        font-weight: 400;
        left: 15px;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        transition: all 0.1s linear;
    }

    input:focus + label, input + label.show, textarea:focus + label, textarea + label.show {
        color: #4981F2;
        font-size: 12px;
        left: -1px;
        top: -28%;
    }
    .wrap-textarea {
        label {
            top: 19%;
        }
        input:focus + label, input + label.show, textarea:focus + label, textarea + label.show {
            top: -11%;
            top: -11%;
        }
    }
}
.main-form {
    background: linear-gradient(191.77deg, #E1EAF5 0%, #F1F5FB 100%);
    padding: 46px 0 56px;
    .title-base {
        text-align: center;
