}
/*checkbox*/
.jq-checkbox {
    display: inline-block;
    width: 24px;
    height: 24px;
    border: 1px solid #c6c8cc;
    border-radius: 4px;
    transition: background ease 0.3s;
    position: relative;
    background: $w;
    overflow: inherit;
    &:hover {
        cursor: pointer;
        background: $w;
    }
    input {
        font-size: 0;
    }
}
.jq-checkbox.checked {
    background: $w;
    .jq-checkbox__div {
        display: block;
    }
}
.jq-checkbox__div {
    position: relative;
    width: 100%;
    height: 100%;
    display: none;
    &:before {
        content: '\e876';
        position: absolute;
        font-family: Material Icons;
        font-size: 21px;
        line-height: 21px;
        color: $or;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        outline: none !important;
    }
}
